<template>
  <div class="container my-5">
    <div class="row justify-content-center">
      <div class="col-6">
        <div class="card mb-5">
          <div class="card-body">
            <h4 class="mb-2">Login</h4>
            <div class="form-group">
              <input v-model="email" type="email" name="" class="form-control" placeholder="Email">
            </div>
            <div class="form-group">
              <input v-model="password" type="password" name="" class="form-control" placeholder="Password">
            </div>
            <button v-on:click='login' class="btn btn-primary">Login</button>
          </div>
        </div>
        
        <div class="card mb-5">
          <div class="card-body">
            <h4 class="mb-2">Add new post</h4>
            <div class="form-group">
              <input v-model="title" type="text" name="" id="" class="form-control" placeholder="Your title">
            </div>
            <div class="form-group">
              <textarea v-model="content" name="" id="" cols="30" rows="10" class="form-control" placeholder="Your content"></textarea>
            </div>
            <button v-on:click='addPost' class="btn btn-primary">Add post</button>
          </div>
        </div>

        <h2>Post List</h2>
        <div v-for="post in posts" class="card mb-3">
          <div class="card-body">
            <h3>{{post.title}}</h3>
            <p v-text="post.content"></p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

  export default {
    data() {
      return {
        message: "Hello world!",
        posts: [],
        title: '',
        content: '',
        email: '',
        password: '',
        token: localStorage.getItem('acc_token')
      }
    },
    mounted() {
      this.getPosts()
    },
    methods: {
      addPost() {
        const data = {
          title: this.title,
          content: this.content
        }

        axios
          .post('https://vueloopback.herokuapp.com/api/Posts?access_token=' + this.token, data)
          .then(res => {
            this.getPosts()
            this.clearInput()
          })
      },
      getPosts() {
        axios
          .get('https://vueloopback.herokuapp.com/api/Posts')
          .then(res => {
            this.posts = res.data
            this.posts.reverse()
          })
      },
      login() {
        const credentials = {
          email: this.email,
          password: this.password
        }

        axios
          .post('https://vueloopback.herokuapp.com/api/Users/login', credentials)
          .then(res => {
            console.log(res)
            this.token = res.data.id
            localStorage.setItem('acc_token', this.token)
          })
      },
      clearInput() {
        this.title = '',
        this.content = ''
      }
    }
  }
</script>




